body {
  color: #fff;
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 8pt;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0px;
  overflow-x: hidden;
  text-align: center;
}

#preload {
  display: none;
}

.page-link {
  cursor: pointer;
}

.disable-hover {
  pointer-events: none;
}

.overflow-wrap {
  overflow-x: hidden;
}

.canvas {
  background: url("../img/filler.png") center center/cover;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100vw;
}

.button {
  border: 2px solid #fff;
  box-sizing: inherit;
  cursor: pointer;
  display: inline-block;
  font-size: 13pt;
  padding: 10px 20px;
  transition: all 0.5s;
}
.button:hover {
  background-color: #04c2c9;
  border-color: #04c2c9;
}
.button .mdi {
  vertical-align: middle;
}

.bold {
  font-family: "Raleway-bold", "Arial", sans-serif;
}

.flex {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.flex.row {
  flex-direction: row;
}
.flex.wrap {
  flex-wrap: wrap;
}

.hex-wrap {
  display: inline-block;
  height: 80px;
  position: relative;
  text-align: center;
  width: 80px;
}

.hexagon {
  background-color: #04c2c9;
  display: inline-block;
  height: 100%;
  width: 57.735%;
}

.hexagon:before {
  background-color: inherit;
  content: "";
  height: inherit;
  position: absolute;
  right: 21.1325%;
  top: 0;
  transform: rotateZ(60deg);
  width: inherit;
}

.hexagon:after {
  background-color: inherit;
  content: "";
  height: inherit;
  position: absolute;
  right: 21.1325%;
  top: 0;
  transform: rotateZ(-60deg);
  width: inherit;
}

.highlight {
  color: #e31b6d;
}

.header {
  color: #444649;
  font-family: "raleway-bold", "Arial", sans-serif;
  font-size: 25pt;
  position: relative;
  position: relative;
}

.header-bar {
  background: #444649;
  height: 4px;
  margin: 25px 0 75px 0;
  width: 70px;
}

.label {
  font-size: 14pt;
  font-weight: 500;
}

.resume {
  background: #455261;
  height: 100px;
}

nav {
  bottom: 0;
  left: 0;
  position: absolute;
  background: #1b242f;
  border-bottom: 3px solid #04c2c9;
  height: 50px;
  text-align: left;
  width: 100%;
  z-index: 99;
}
nav.fixed {
  bottom: inherit;
  left: 0;
  position: fixed;
  top: 0;
}
nav.fixed.desk {
  animation: popDown 0.5s;
}
nav .link-wrap {
  background: #333;
  height: 0;
  max-width: 1200px;
  overflow: hidden;
  position: absolute;
  top: 53px;
  transition: height 0.3s ease-out;
  width: 100%;
}
nav .link-wrap.visible {
  height: 220px;
}
nav .link-wrap div {
  cursor: pointer;
  font-size: 12pt;
  padding: 12px 20px;
  text-transform: uppercase;
  transition: color 0.5s;
}
nav .link-wrap div:first-child {
  padding: 25px 20px 12px 20px;
}
nav .link-wrap div:hover {
  color: #e31b6d;
}
nav .mdi-menu {
  font-size: 30pt;
  line-height: 36pt;
  margin: auto 0;
  position: absolute;
  right: 5vw;
  top: 0;
}
nav .toggle-theme-wrapper * {
  box-sizing: border-box;
}
nav .toggle-theme-wrapper .label {
  background-color: #111;
  border-radius: 50px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  position: relative;
  height: 26px;
  width: 50px;
  transform: scale(1.5);
}
nav .toggle-theme-wrapper .label .mdi:nth-child(1) {
  position: absolute;
  left: 1px;
  color: #f1c40f;
}
nav .toggle-theme-wrapper .label .mdi:nth-child(2) {
  position: absolute;
  right: 0;
  color: #f39c12;
}
nav .toggle-theme-wrapper .checkbox {
  opacity: 0;
  position: absolute;
}
nav .toggle-theme-wrapper .label .ball {
  background-color: #f39c12;
  border-radius: 50%;
  position: absolute;
  top: 2px;
  left: 2px;
  height: 22px;
  width: 22px;
  transform: translateX(0px);
  transition: transform 0.5s linear;
}
nav .toggle-theme-wrapper .checkbox:checked + .label .ball {
  transform: translateX(24px);
  background: #f1c40f;
  transition: all 0.5s linear;
}
nav .active {
  color: #e31b6d;
}

.carousel-wrap {
  height: 250px;
  margin: 0 auto;
  position: relative;
}

.window {
  background: #222;
  overflow: hidden;
  position: relative;
}

#carousel {
  position: relative;
  top: 0;
  width: 10000px;
}

.slide {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  float: left;
  height: 250px;
  justify-content: center;
}

.mdi-chevron-left,
.mdi-chevron-right {
  background: rgba(0, 0, 0, 0.3);
  bottom: 0;
  color: #fff;
  cursor: pointer;
  cursor: pointer;
  font-size: 14pt !important;
  font-weight: bold;
  padding: 10px 15px;
  position: absolute;
}

.mdi-chevron-left {
  left: 0;
}

.mdi-chevron-right {
  right: 0;
}

.transition {
  transition: 0.7s;
}

.modal-wrap {
  height: 100vh;
  pointer-events: none;
  position: fixed;
  width: 100vw;
  z-index: 100;
}
.modal-wrap .mask {
  background: rgba(0, 0, 0, 0.5);
  height: 100%;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  transition: opacity 0.3s;
  width: 100%;
}
.modal-wrap .modal {
  align-items: flex-start;
  background: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  box-sizing: border-box;
  color: #333;
  height: 100vh;
  opacity: 0;
  position: relative;
  text-align: left;
  transform: scale(0.8);
  transition: all 0.3s;
}
.modal-wrap .close {
  bottom: 25px;
  color: #bbb;
  cursor: pointer;
  font-size: 20pt;
  font-weight: 800;
  position: absolute;
  right: 10px;
}
.modal-wrap .info-box {
  border-top: 3px solid #1b242f;
  box-sizing: border-box;
  color: #444;
  padding: 15px 10px 85px 10px;
}
.modal-wrap .title {
  font-family: "Raleway-bold", "Arial", sans-serif;
  font-size: 16pt;
  line-height: 20pt;
}
.modal-wrap .tag {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  color: #c0c0c0;
  display: block;
  font-family: "Raleway-bold", "Arial", sans-serif;
  font-size: 8pt;
  font-weight: 300;
  padding-bottom: 15px;
}
.modal-wrap .detail {
  font-size: 9pt;
  line-height: 14pt;
  margin-top: 13px;
}
.modal-wrap .mdi-open-in-new {
  font-size: 11pt;
  left: -10px;
  position: relative;
}
.modal-wrap .button {
  background: #e31b6d;
  border: 0;
  bottom: 20px;
  color: #fff;
  cursor: pointer;
  font-size: 10pt;
  font-weight: 600;
  left: 10px;
  padding: 11px 30px;
  pointer-events: none;
  position: absolute;
  transition: all 0.5s;
  visibility: hidden;
}
.modal-wrap .button.visible {
  pointer-events: initial;
  visibility: visible;
}
.modal-wrap .button:hover {
  background: #fff;
  color: #e31b6d;
}
.modal-wrap .carousel {
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.25);
}
.modal-wrap.visible {
  pointer-events: initial;
}
.modal-wrap.visible .modal {
  opacity: 1;
  transform: scale(1);
}
.modal-wrap.visible .mask {
  opacity: 1;
}

section {
  color: #616161;
  line-height: 11pt;
  padding: 70px 0 70px 0;
  position: relative;
}
section .container {
  box-sizing: border-box;
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 10px;
  width: 100%;
}
section:nth-of-type(1) {
  color: #fff;
  height: calc(100vh + 53px);
  padding: 0 10px;
}
section:nth-of-type(1) .highlight {
